.job-card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-left: 4px solid #007bff;
  transition: all 0.2s ease;
  cursor: grab;
  position: relative;
}

.job-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}

.job-card:active {
  cursor: grabbing;
}

.job-card.archived {
  border-left-color: #6c757d;
  opacity: 0.8;
}

.job-card.archived:hover {
  opacity: 1;
}

.job-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.job-title {
  flex: 1;
}

.job-title h3 {
  margin: 0 0 8px 0;
  color: #333;
  font-size: 1.3rem;
  font-weight: 600;
  line-height: 1.3;
}

.status-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.active {
  background-color: #d4edda;
  color: #155724;
}

.status-badge.archived {
  background-color: #f8d7da;
  color: #721c24;
}

.job-actions {
  display: flex;
  gap: 8px;
  margin-left: 15px;
}

.btn-sm {
  padding: 6px 10px;
  font-size: 0.8rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-sm:hover {
  transform: scale(1.1);
}

.btn-warning {
  background-color: #ffc107;
  color: #212529;
}

.btn-warning:hover {
  background-color: #e0a800;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-success:hover {
  background-color: #218838;
}

.job-content {
  margin-bottom: 15px;
}

.job-meta {
  display: flex;
  gap: 15px;
  margin-bottom: 10px;
  font-size: 0.9rem;
  color: #666;
}

.job-slug {
  font-family: 'Courier New', monospace;
  background-color: #f8f9fa;
  padding: 2px 6px;
  border-radius: 3px;
}

.job-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 10px;
}

.tag {
  background-color: #e9ecef;
  color: #495057;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 500;
}

.job-dates {
  display: flex;
  gap: 15px;
  font-size: 0.8rem;
  color: #999;
}

.drag-handle {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #ccc;
  font-size: 1.2rem;
  cursor: grab;
  padding: 5px;
}

.drag-handle:hover {
  color: #999;
}

/* Responsive design */
@media (max-width: 768px) {
  .job-card {
    padding: 15px;
  }
  
  .job-header {
    flex-direction: column;
    gap: 10px;
  }
  
  .job-actions {
    margin-left: 0;
    align-self: flex-end;
  }
  
  .job-meta {
    flex-direction: column;
    gap: 5px;
  }
  
  .job-dates {
    flex-direction: column;
    gap: 5px;
  }
  
  .drag-handle {
    position: static;
    transform: none;
    text-align: center;
    margin-top: 10px;
  }
}

